.App {
  text-align: center;
  position: relative;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  height: 65px;
  padding: 20px;
  color: white;
  position: fixed;;
  top: 35px;
  right:0;
  -webkit-transition: 0.3s ease-in-out;
  -o-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
}

.App-header::before{
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  height: 0;
  width: 100%;
  -webkit-transition: 0.3s ease-in-out;
  -o-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
  z-index: -1;
}
.App-header-at-top::before{
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  height: 65px;
  width: 100%;
  -webkit-transition: 0.3s ease-in-out;
  -o-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
  z-index: -1;
  background: #fff;
  -webkit-box-shadow: 2px 0 8px rgba(0, 0, 0, 0.25);
          box-shadow: 2px 0 8px rgba(0, 0, 0, 0.25);
}
.App-header-at-top {
  height: 65px;
  padding: 20px;
  color: white;
  position: fixed;;
  top: 0px;
  right:0;
  -webkit-transition: 0.3s ease-in-out;
  -o-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
}
.App-header ul, .App-header-at-top ul{
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
}
.App-header ul li,.App-header-at-top ul li{
  float: left;
}
.App-title {
  font-size: 1.5em;
}

section{
  background-color: white;
}

.App-intro {
  font-size: large;
}
.App-header li a, .App-header-at-top li a{
  padding: 10px 15px;
  opacity: 0.8;
  color: gray;
  text-decoration: none;
  font-size: 13;
  -webkit-transition: 0.3s ease-in-out;
  -o-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
}
.App-header a:hover, .App-header-at-top a:hover{
  opacity: 1
}
.App-header .is-current a{
  color: white;
}
.App-header-at-top .is-current a{
  color: black;
}

.page-cover {
    width: 100%;
    position: fixed;
    height: 120%;
    height: 100vh;
    top: 0;
    left: 0;
    z-index: -1;
    overflow: hidden;
}
.page-cover .cover-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
}

.page-cover .cover-bg-mask {
    background-image: url("./img/bg-layer.png");
    //background-image: url("./img/cover.jpg");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.navbar-brand {
  position: fixed;
  left: 70px;
  text-decoration: none;
  font-size: 13;
  -webkit-transition: 0.3s ease-in-out;
  -o-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
  display: -ms-flexbox;
  display: flex;
}
.navbar-brand .text {
  font-size: 120%;
  display:-ms-flexbox;
  display:flex;
  -webkit-transition: 0.3s ease-in-out;
  -o-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
}
.navbar-brand img {
  height: 50px;
}
.App-header-at-top .navbar-brand{
  top:5px;
}
.App-header .navbar-brand{
  top:35px;
}

.App-header-at-top .navbar-brand .text{
  color: black;
}
.App-header .navbar-brand .text{
  color: white;
}



